body{
    min-height: 100%;
    height: 100%;
}

.mar-container{
    height: 100vh;
    width: 100%;
    align-items: center;
    background-color: rgb(237, 29, 36);
}

.mar-box{
    padding-top: 1.7%;
}

.mar-slide{
    width: 50%;
    height: 100%;
    top: 1%;
    left: 1%;
    text-align: center;
    float: left;
    background-color: rgb(237, 29, 36);
}

.mar-image-container-hero{
    width: 50%;
    height: 100%;
    display: flex;
}

.mar-image-container-villain{
    width: 50%;
    height: 100%;
    display: flex;
    float: right;
}

.mar-img{
    border-radius: 2% 0 0 2%;
}

.mar-desc-hero{
    background-image: linear-gradient(to right, rgb(237, 29, 36), 
    rgba(20, 60, 116, 0.8) 50%);
    width: 50%;
    height: 100%;
    position: absolute;
    right: 0;
    color: white;
    overflow-wrap: break-word;
    font-size: 1vw;
    text-align: left;
    border-radius: 0 2% 2% 0;
    padding-right: 5%;
    padding-left: 3%;
}

.mar-desc-villain{
    background-image: linear-gradient(to right, rgb(237, 29, 36), 
    rgba(0, 22, 117, 1) 50%);
    width: 50%;
    height: 100%;
    position: absolute;
    left: 0;
    color: white;
    overflow-wrap: break-word;
    font-size: 1vw;
    text-align: left;
    border-radius: 2% 0 0 2%;
    padding-right: 5%;
    padding-left: 3%;
}

.legend{
    z-index: 1;
    font-size: 1.2vw   !important;
}

.thumb{
    max-height: 110px;
    height: 70%;
    width: auto;
}

.marvel-vote, .marvel-home, .marvel-dc{
    width: 5%;
    height: 5%;
    top: 0;
    position: absolute;
    color: rgb(0, 22, 117);
    text-align: center;
    font-size: 1vw;
}

.marvel-vote:hover{
    color: black;
    font-size: 1.3vw;
}

.marvel-home:hover{
    color: black;
    font-size: 1.3vw;
}

.marvel-dc:hover{
    color: black;
    font-size: 1.3vw;
}

.marvel-vote{
    z-index: 0;
    left: 0;
}

.marvel-home{
    left: 50%;
}

.marvel-dc{
    right: 0;
}

@media only screen and (max-width:860px){

    .mar-slide{
        width: 100%;
    }
    
    .mar-desc-hero{
        font-size: 1.5vw;
    }
    
    .mar-desc-villain{
        font-size: 1.5vw;
    }

    .thumbs-wrapper.axis-vertical{
        display: none;
    }

  }